import React, { useState, useEffect ,useRef} from "react";
import "bootstrap/dist/css/bootstrap.css";
import AlHeder from "../comp/AlHeder";
import Body from "./Body";
import Futer from "./Futer";
import data from "./data.json";
import "./Futer.css";


function App() {
  const [mmm, setmmm] = useState(0);
  const [idAnser, setIdAnser] = useState(0);
  const theDatas = data.body.questions[idAnser].data.answers;
  const theDatass = data.body.questions[idAnser].data.answers;
  const dataQuastion = data.body.questions[idAnser].data.question;
  const theDa = data.body.questions[mmm].data.answers;
  const [array, setArray] = useState(theDatass);
  const [theda, setTheda] = useState(theDatass);
  const [arraya, setArraya] = useState(0);
  const [anserid, setAnserid] = useState(0);
  const [numbers, setnumbers] = useState(0);
  const [flagNext, setFlagNext] = useState(true);
  const [mermermer, setmermermre] = useState(0);
  const trueCount = theDatas.reduce((count, obj) => {
    if (obj.correct && !obj.solved) {
      return count + 1;
    } else {
      return count;
    }
  }, 0);
  const onClickNext = () => {
    setIdAnser(idAnser + 1);
    setAnserid(0);
    setArray(array);
    setTheda(theda);
  };
  useEffect(() => {
    setArray(theDatass);
    setTheda(theDatass);
    setmermermre(mermermer + 1);
    setArraya(trueCount);
    setmmm(idAnser);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [idAnser]);
  useEffect(() => {
    const shuffledItems = [...theda];
    shuffledItems.sort(() => Math.random() - 0.5);
    setTheda(shuffledItems);
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [mermermer]);
  const handleReplace = (element, ids) => {
    if (array[anserid].text !== null) {
      if (numbers <= trueCount - 2) {
        if (array[anserid + 1].solved === false) {
          setAnserid(anserid + 1);
        } else if (array[anserid + 2].solved === false) {
          setAnserid(anserid + 2);
        } else if (array[anserid + 3].solved === false) {
          setAnserid(anserid + 3);
        } else if (array[anserid + 4].solved === false) {
          setAnserid(anserid + 4);
        } else if (array[anserid + 5].solved === false) {
          setAnserid(anserid + 5);
        } else if (array[anserid + 6].solved === false) {
          setAnserid(anserid + 6);
        } else if (array[anserid + 7].solved === false) {
          setAnserid(anserid + 7);
        } else if (array[anserid + 8].solved === false) {
          setAnserid(anserid + 8);
        } else if (array[anserid + 9].solved === false) {
          setAnserid(anserid + 9);
        } else if (array[anserid + 10].solved === false) {
          setAnserid(anserid + 10);
        } else if (array[anserid + 11].solved === false) {
          setAnserid(anserid + 11);
        } else if (array[anserid + 12].solved === false) {
          setAnserid(anserid + 12);
        } else if (array[anserid + 13].solved === false) {
          setAnserid(anserid + 13);
        } else {
          setAnserid(anserid + 14);
        }
      } else {
        setAnserid(anserid + 1);
      }
      setnumbers(numbers + 1);
      const newtheData = theda.map((item, i) => {
        if (i === ids) {
          return { ...item, thecolor: true };
        }
        return item;
      });
      setTheda(newtheData);
      const newArray = array.map((item, id) => {
        if (id === anserid && item.correct && item.text !== null) {
          return { ...element, solved: true, order: ids, correct: true };
        }
        return item;
      });
      setArray(newArray);
    }
  };
  const [mera, setMera] = useState(25);
  const handleReReplace = (element, ids) => {
    if (mera >= ids) {
      setAnserid(ids);
      setMera(ids);
    }
    setnumbers(numbers - 1);
    if (element.text !== null) {
      const newtheData = theda.map((item, i) => {
        if (i === element.order) {
          return { ...item, thecolor: false };
        }
        return item;
      });
      setTheda(newtheData);
      const newArray = array.map((item, id) => {
        if (id === ids) {
          return { ...element, solved: false };
        }
        return item;
      });
      setArray(newArray);
    } else {
      const newtheData = theda.map((item, i) => {
        if (i === element.order) {
          return { ...item, thecolor: false };
        }
        return item;
      });
      setTheda(newtheData);
      const newArray = array.map((item, id) => {
        if (id === ids) {
          return { ...element, solved: false };
        }
        return item;
      });
      setArray(newArray);
    }
  };

  //
  //
    const audioRef = useRef(null);
  const playSound = () => {
      audioRef.current.src = dataQuastion.sound;
      audioRef.current.play();
  };
  

  return (
    <div
      className="container "
      onLoad={() => {
        setArraya(trueCount);
      }}
    >
      <AlHeder />
      <Body
        array={array}
        anserid={anserid}
        handleReReplace={handleReReplace}
        flagNext={flagNext}
        theDatas={theDa}
        dataQuastion={dataQuastion}
        playSound={playSound}
        audioRef={audioRef}
      />
      <Futer
        theData={theda}
        onClickNext={onClickNext}
        handleReplace={handleReplace}
        arraya={arraya}
        setnumbers={setnumbers}
        numbers={numbers}
        setMera={setMera}
        setFlagNext={setFlagNext}
        flagNext={flagNext}
      />
    </div>
  );
}

export default App;
